	import product from '@/static/mbcImg/publish/createBusiness/rhx.png';
	import date from '@/static/mbcJs/dateTime.js';
	export default {
	    data () {
			return {
				product: product
			};
	    },
		props: ['msgData'],
		created() {
		},
		filters: {
          /* 格式化时间戳 */
          dateTime (val) {
            return date.dateTime('.', val);
          }
        },
	    methods: {
			goToTemplatePro(e) {
				console.log('to投放商机');
				let urlParams = {
					isFlow: 0, // 平台来源
					orderType: 0, // 订单类型
					sourceTemp: 0, // 说明书来源
					aiListTemp: 0, // 是否展示ai
					id: e.id, // 模板id
					isMenu: e.isMenu, // 横向导航
					instrucId:  null, // 说明书id
					lookUserId: null, // 浏览者
					creatorId: null, // 说明书创建者
					instrucState: null // 说明书状态
				};
				uni.navigateTo({
					url: '/modules/createBusiness/templatePro/templatePro?urlParams=' + this.urlCrypto(urlParams, 0)
				});
			},
	    }
	};
